import React, { Component } from 'react'
import PureRenderMixin from 'react-addons-pure-render-mixin'
import ReactSwipe from 'react-swipe'

import './style.css'

class Category extends Component {
  constructor (props) {
    super(props)
    this.shouldComponentUpdate = PureRenderMixin.shouldComponentUpdate.bind(this)
    this.state = {
      index: 0
    }
  }

  render () {
    const options = {
      auto: 4000,
      callback: (index) => {
        this.setState({
          index
        })
      }
    }

    return (
      <div className="home-cateory">
        <ReactSwipe swipeOptions={options}>
          <div className="carousel-item">
            <ul>
              <li><i className="item-food"></i><span></span></li>
              <li><i className="item-movie"></i><span></span></li>
              <li><i className="item-hotel"></i><span></span></li>
              <li><i className="item-entertainment"></i><span></span></li>
              <li><i className="item-take-out-food"></i><span></span></li>
              <li><i className="item-hot-pot"></i><span></span></li>
              <li><i className="item-beauty"></i><span></span></li>
              <li><i className="item-around"></i><span></span></li>
              <li><i className="item-ktv"></i><span>KTV</span></li>
              <li><i className="item-hunshasheying"></i><span></span></li>
            </ul>
          </div>
          <div className="carousel-item">
            <ul>
              <li><i className="item-life-service"></i><span></span></li>
              <li><i className="item-tourist"></i><span></span></li>
              <li><i className="item-aiche"></i><span></span></li>
              <li><i className="item-sport"></i><span></span></li>
              <li><i className="item-buy"></i><span></span></li>
              <li><i className="item-children"></i><span></span></li>
              <li><i className="item-furniture"></i><span></span></li>
              <li><i className="item-study"></i><span></span></li>
              <li><i className="item-health"></i><span></span></li>
              <li><i className="item-home"></i><span></span></li>
            </ul>
          </div>
          <div className="carousel-item">
            <ul>
              <li><i className="item-xiaochi"></i><span></span></li>
              <li><i className="item-zizhu"></i><span></span></li>
              <li><i className="item-tokyo-food"></i><span></span></li>
              <li><i className="item-hair"></i><span></span></li>
              <li><i className="item-meijiameitong"></i><span></span></li>
              <li><i className="item-spa"></i><span>SPA</span></li>
              <li><i className="item-shoushen"></i><span></span></li>
              <li><i className="item-qinzisheying"></i><span></span></li>
              <li><i className="item-qinziyoule"></i><span></span></li>
              <li><i className="item-all-category"></i><span></span></li>
            </ul>
          </div>
        </ReactSwipe>
        <div className="carousel-index">
          <span className={this.state.index === 0 ? 'active': ''}></span>
          <span className={this.state.index === 1 ? 'active': ''}></span>
          <span className={this.state.index === 2 ? 'active': ''}></span>
        </div>
      </div>
    )
  }
}

export default Category



